.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  position: relative;
  background-color: var(--main-color);
  color: white;
  border-radius: 14px;
  padding: 2rem;
  margin-top: 50px;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
}
