.form button {
  background-color: var(--main-color);
  border: 1px solid var(--main-color);
  color: white;
  padding: 0.25rem 2rem;
  border-radius: 1rem;
  font-weight: bold;
}

.form button:hover,
.form button:active {
  background-color: var(--second-color);
  border-color: white;
}
