.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid var(--main-color);
  padding: 1rem 0;
  margin: 1rem 0;
}

.cart-item h2 {
  margin: 0 0 0.5rem 0;
  color: #363636;
}

.cart-item .summary {
  width: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-item .price {
  font-weight: bold;
  color: black;
}

.cart-item .amount {
  font-weight: bold;
  border: 1px solid #ccc;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  color: #363636;
}

.cart-item .actions {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .cart-item .actions {
    flex-direction: row;
  }
}

.cart-item button {
  font: inherit;
  padding: 10px;
  font-weight: bold;
  font-size: 1.25rem;
  color: var(--main-color);
  border: 1px solid var(--main-color);
  width: 3rem;
  text-align: center;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;
  margin: 0.25rem;
}

.cart-item button:hover,
.cart-item button:active {
  background-color: var(--main-color);
  color: white;
}
